*{
    overflow-x: hidden;
}



.main{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    width: 100%;
}
#commitment-line{
    margin: 100px 0;
    padding: 16px 50px;
    width: 100vw;
    margin-bottom: 2px;
    margin-top: 320px;
    /* position: relative; */
}
#main-section{
    background-color: #000000;
    z-index: 10;
    /* position: relative; */
}
.commitmentkero{
    font-size: 12px;
}
.hide-main-section{
    display: none;
}
.position-absolute{
    position: absolute;
}
.position-relative{
    position: relative;
}
.d-flex{
    display: flex;
}
.align-center{
    align-items: center;
}
.z-2{
    z-index: 2;
}
.bg-black{
    background-color: #000;
}
.animate-slide-up{
    transition: 1.6s linear;
    animation: slidein 1.6s ;
    -webkit-transition: 1.6s linear;
    -moz-transition: 1.6s linear;
    -o-transition: 1.6s linear;
    -webkit-animation: slidein 1.6s ;
    -moz-animation: slidein 1.6s ;
    -o-animation: slidein 1.6s ;
    animation-fill-mode: forwards;
}
.video-init{
    transform: translate(0, 20em);
    -webkit-transform: translate(0s, 20em);
}
.slidein {
    -webkit-animation-delay: 15s; /* Safari 4.0 - 8.0 */
    -moz-animation-delay: 15s;
    animation-delay: 15s;
  }
  
  @-moz-keyframes slidein {
    from {
        -moz-transform:  translate(0s, 20em);
    }
    to {
        -moz-transform: translate(0em, 0);
    }
  }
  
  @-webkit-keyframes slidein {
    from {
        -webkit-transform: translate(0s, 20em);
    }
    to {
        -webkit-transform: translate(0em, 0);
    }
  }
  
  @keyframes slidein {
    from {
        transform: translate(0, 20em);
    }
    to {
        transform: translate(0em, 0);
    }
  }
